<script>
import page from "./v.s.page.vue"
export default {
    name:'SCREEN',
    extends:page,
    computed: {
        background()
        {
            return [
                {
                    //i:'url(https://www.artsfon.com/pic/201511/2560x1600/artsfon.com-75888.jpg)',
                   // blur:true,
                }
            ]
        }
    },
}
</script>
<style lang="less">
.PAGE.ROOT.SCREEN
{
    position: fixed ;
    overflow: hidden ;
    z-index: 0;
    width:100vw;
    height: 100vh;
    & > #page-background-0
        {
            background-image:url(../assets/wall/k2.jpg) !important;
        }   
}
</style>



